import { Divider, Grid, Typography } from '@material-ui/core'
import React, { useState } from 'react'
import { NavigationReference } from '../../../../Navigation/NavigationRefrence'
import { Link } from 'react-router-dom'
// import TableUtils from '../../../../TableUtils'
export default function AvailableAnimations() {
  const head = [
    { title: 'Animation', key: 'Animation', width: '20%' },
    { title: 'What it does', key: 'use', width: '80%' },
  ]
  return (
    <Grid container item xs={12} style={{ overflowX: 'hidden' }}>
      <Grid
        container
        spacing={3}
        item
        xs={12}
        justifyContent="center"
        // style={{ textAlign: 'center' }}
      >
        <Grid item xs={12}>
          <Typography variant="h1">Available Animations</Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="h3">
            {' '}
            We give a wide range of Animations for you to select from and use
            what that suits best to you.
            <br />
            <br />
            <Typography variant="h6" style={{ textAlign: 'center' }}>
              To see the How to Use these animations{' '}
              <Link
                to={NavigationReference.Animations.index}
                style={{ color: '#0EDA3B' }}
              >
                CLICK HERE
              </Link>
            </Typography>
          </Typography>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <br />

        <Divider
          orientation="vertical"
          style={{ width: '100%', height: '1px' }}
        />
        <br />
      </Grid>
      {/* <Grid item xs={12} style={{ overflowX: 'scroll' }}>
        <TableUtils head={head} />
      </Grid> */}
    </Grid>
  )
}
